<template>
  <div class="withdraw-account" style="padding-top: 44px;">
    <back-header title="管理提现账户" />
    <div class="input">
      <label>支付宝姓名</label>
      <input type="text" v-model="form.alipay_name" placeholder="请输入支付宝姓名">
    </div>
    <div class="input">
      <label>支付宝账号</label>
      <input type="text" v-model="form.alipay_account" placeholder="请输入支付宝账号">
    </div>
    <div class="input">
      <label>手机号</label>
      <input v-model="phone" type="text" placeholder="请输入手机号">
    </div>
    <div class="input">
      <label>验证码</label>
      <input type="text" v-model="form.smscode" placeholder="请输入手机验证码">
      <span @click="sendCode">{{ countText }}</span>
    </div>
    <div class="bottom-btn" @click="submit">立即修改提现账户</div>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import { post } from '../../common/js/http'

export default {
  name: 'withdraw-account',
  components: {
    BackHeader
  },
  data() {
    return {
      form: {
        alipay_account: '',
        alipay_name: '',
        smscode: ''
      },
      phone: '',
      codeTime: 60,
      btnDefaultText: '获取验证码'
    }
  },
  computed: {
    countText () {
      if (this.codeTime < 60 && this.codeTime > 0) return `${this.codeTime}s`
      return this.btnDefaultText || '获取验证码'
    }
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods: {
    sendCode() {
      if (!this.phone) {
        this.$toast.fail('请输入手机号')
        return
      }
      if (this.timer) return
      post('/webapi/user/sendVerifyCode', { mobile: this.phone })
        .then(() => {
          this.countDown()
        })
    },
    countDown () {
      this.$toast.success('发送成功')
      this.timer = setInterval(() => {
        if (this.codeTime <= 0) {
          clearInterval(this.timer)
          this.timer = null
          this.codeTime = 60
          return
        }
        this.codeTime--
      }, 1000)
    },
    submit() {
      let msg = ''
      if (!this.form.alipay_name) msg = '请输入支付宝姓名'
      if (!msg && !this.form.alipay_account) msg = '请输入支付账号'
      if (!msg && !this.form.smscode) msg = '请输入验证码'
      if (msg) {
        this.$toast.fail(msg)
        return
      }
      const loading = this.$toast.loading()
      post('/webapi/withdraw/editCashAccount', this.form)
        .then(() => {
          loading.close()
          this.$router.goBack()
        })
        .catch(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
    }
  }
}
</script>

